.Form {
  max-width: 700px;
  width: 100%;
  padding: 0 2em;
  height: 160px;
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
          flex-direction: column;
  -webkit-box-align: center;
          align-items: center;
  align-content: center;
  justify-content: space-around;
}

  .Form__label {
    display: -webkit-box;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
            flex-direction: column;
    width: 100%;
  }

  .Form__label-name {
    color: var(--color-font-light);
  }

  .Form__input {
    border: 1px solid var(--color-font-light);
    padding: 0.4em 1em;
    margin: 0.4em 0;
    border-radius: 0.4em;
    height: 2.6em;
  }

  .Form__input:focus {
      outline-color: var(--color-secondary);
    }

  .Form__submit {
    max-width: 400px;
    font-size: 1.4em;
    width: 100%;
    padding: 0.4em;
    border-radius: 0.6em;
    background: var(--color-secondary);
    color: var(--color-font-contrast);
    cursor: pointer;
    outline: none;
  }

  .Form__submit[disabled] {
      background: var(--color-secondary-light);
      cursor: not-allowed;
    }
